/* Quiz Control styling */
#quiz-controls {
    grid-row: 1; 
    grid-column:3/6;
}

#quiz-options {
    grid-row: 1; 
    grid-column:6/8;
}
#quiz-items {
    grid-row: 2/6; 
    grid-column:6/8;
}
#empty{
    grid-column: 1/3;
    grid-row: 2/7;
}

.quiz-grid-container{
    display: grid;
    max-height: 118px;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(2, 1fr); /* Adjust rows as needed */
}

.quiz-grid-item{
    margin: auto;
    text-align:center;
    padding: 5px;
}

#quiz-controls-table{
    height:100%;
    width:100%;
}

#quiz-stop-start{
    grid-row:1/3;
    grid-column:2;
    height:auto;
    width:auto;
}

#timer {
    display:none;
    grid-row:1;
    grid-column:1;
    font-size: 30px;
    color: #53b324;
}

#give-up-btn {
    display:none;
    grid-row:2;
    grid-column:1;
    font-size: 14px;
    background-color: #dc3545;
    border: 1px dashed #fff;
    color: #f0f0f0;
    margin: 10px;
    height:auto;
    width:auto;
}

#answer-box-container{
    text-align: left;
    grid-row:1;
    grid-column:3/6;
    display: none;
}

#answer-box-input{
    width:100%;
}

#answer-box-prompt{
    color:#f0f0f0;
}

#country-counter{
    display:none;
    text-align:left;
    grid-row:2;
    grid-column:3/5;
    color: #f0f0f0;
    margin:0;
}

#checkbox-container{
    color:#f0f0f0;
}

#country-name-container{
	color:#f0f0f0
}